.contact_section {
  @extend .container;
  margin: auto;
}

.contact__title {
  font-family: $mainFont;
  font-size: 24px;
  line-height: 2.1;
  text-align: center;
  color: $mainColor;
}

.contact__Text {
  padding-top: 0px;
  padding-bottom: 22px;
  font-family: $secondaryFont;
  font-size: 16px;
  line-height: 2.1;
  text-align: center;
  color: $mainColor;
}

.contact__form {
  width: 270px;
  margin: auto;

  @media screen and (min-width: 769px) {
    width: 410px;
  }
}

.contact__form_input {
  display: block;
  width: 240px;
  height: 20px;
  margin: auto;
  margin-bottom: 25px;
  border: none;
  border-bottom: 1px solid $brandColor;
  font-family: $secondaryFont;
  font-size: 16px;
  line-height: 1.25;
  color: rgba(171, 129, 108, 0.5);

  @media screen and (min-width: 769px) {
    width: 410px;
  }
}
.contact__form_button {
  @extend .registration__form_button;
  margin: auto;
  margin-top: 37px;
}
